:root {
  /* Tema dark minimalista */
  --bg: #05070b;
  --bg-soft: #0b0f16;
  --card: #101620;
  --border: #222a36;
  --text: #f5f7fb;
  --muted: #9aa3b5;
  --accent: #3b82f6;

  /* Espaçamentos responsivos */
  --container-padding: 16px;

  @media (min-width: 768px) {
    --container-padding: 32px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  font-size: 16px;
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

body,
#root {
  min-height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family:
    system-ui,
    -apple-system,
    Segoe UI,
    Roboto,
    Ubuntu,
    Cantarell,
    'Helvetica Neue',
    Arial,
    'Noto Sans',
    'Apple Color Emoji',
    'Segoe UI Emoji',
    'Segoe UI Symbol';
  background: var(--bg);
  color: var(--text);
  line-height: 1.5;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

a {
  color: inherit;
  text-decoration: none;
}

.navbar {
  position: sticky;
  top: 0;
  z-index: 50;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px var(--container-padding);
  background: rgba(5, 7, 11, 0.98);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  height: 64px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.2);

  @media (min-width: 768px) {
    height: 72px;
    padding: 15px var(--container-padding);
  }
}

.navbar--scrolled {
  background: rgba(5, 7, 11, 0.98);
}

.navbar__brand {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.navbar__brand:hover {
  transform: scale(1.02);
}

.brand-mark {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 18px;
}

.navbar__nav {
  display: flex;
  gap: 24px;
  margin: 0 auto;
  padding: 0 20px;
}

.navbar__link {
  background: transparent;
  border: none;
  color: var(--muted);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 6px;
  transition: all 0.2s ease;
  position: relative;
  letter-spacing: 0.5px;
}

.navbar__link:hover {
  color: var(--text);
  background: rgba(255, 255, 255, 0.05);
}

.navbar__link--active {
  color: var(--text);
  font-weight: 600;
}

.navbar__link--active::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 4px;
  background: var(--accent);
  border-radius: 50%;
}

.navbar__right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.navbar__icon {
  background: transparent;
  border: none;
  color: var(--muted);
  cursor: pointer;
  font-size: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.navbar__icon:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text);
  transform: scale(1.1);
}

.banner {
  position: relative;
  height: 260px;
  background: #0b0f16;
  display: flex;
  align-items: flex-end;
}

.banner__content {
  padding: 24px;
}

.banner__title {
  margin: 0 0 6px;
  font-size: 32px;
}

.banner__subtitle {
  margin: 0;
  color: var(--muted);
}

.banner__fade {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80px;
  background: linear-gradient(180deg, rgba(15, 15, 15, 0) 0%, var(--bg) 100%);
}

/* layout-main não é mais usado nas telas principais, mantido apenas se algo legado ainda referenciar */
.layout-main {
  display: flex;
}

.layout-main__content {
  flex: 1;
}

/* Sidebar hoje é secundária; estilo simples e discreto */
.sidebar {
  padding: 12px 8px;
  background: var(--bg);
  border-right: 1px solid var(--border);
}

.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.sidebar__nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar__nav--icons {
  margin-top: 8px;
}

.sidebar__avatar {
  width: 32px;
  height: 32px;
  border-radius: 999px;
  background: var(--card);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

.sidebar__avatar--large {
  width: 40px;
  height: 40px;
}

.sidebar__icon-btn {
  width: 40px;
  height: 40px;
  border-radius: 14px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  color: var(--muted);
  padding: 0 8px;
}

.sidebar__icon-btn--active {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text);
}

.sidebar__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  font-size: 15px;
}

.sidebar__icon-label {
  margin-left: 0;
  opacity: 0;
  white-space: nowrap;
  font-size: 13px;
  color: var(--muted);
  transform: translateX(-8px);
  transition:
    opacity 150ms ease-out,
    transform 150ms ease-out,
    margin-left 150ms ease-out;
}

.sidebar:hover .sidebar__icon-label {
  opacity: 1;
  margin-left: 10px;
  transform: translateX(0);
}

/* Sem ::before: usamos diretamente o caractere unicode como ícone */
.sidebar__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar__footer--minimal {
  border-top: none;
  padding-top: 0;
}

/* Animações antigas removidas para manter o visual minimalista */

.content {
  flex: 1;
  padding: 16px 0 40px;
  width: 100%;
  max-width: 100%;
  overflow: hidden;

  @media (min-width: 768px) {
    padding: 24px 0 60px;
  }
}

/* Melhorias para telas pequenas */
@media (max-width: 480px) {
  html {
    font-size: 15px;
  }

  .navbar__brand {
    font-size: 1.2rem;
  }

  .navbar__logo {
    width: 28px;
    height: 28px;
  }
}

/* Melhorias para o scroll suave */
html {
  scroll-behavior: smooth;
}

/* Melhorias de acessibilidade */
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

/* Previne quebra de layout em telas muito pequenas */
body {
  min-width: 320px;
}

/* Melhora a legibilidade em dispositivos móveis */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
  line-height: 1.3;
}

/* Ajustes para imagens responsivas */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

.auth-container {
  min-height: calc(100vh - 56px);
  display: grid;
  place-items: center;
  padding: 24px;
}

.auth-card {
  width: 100%;
  max-width: 380px;
  background: var(--bg-soft);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 24px;
}

.auth-card h1 {
  margin: 0 0 6px;
  color: var(--accent);
}

.muted {
  color: var(--muted);
  margin: 0 0 16px;
}

.input {
  width: 100%;
  background: var(--card);
  border: 1px solid var(--border);
  color: var(--text);
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 12px;
}

.btn {
  background: var(--card);
  border: 1px solid var(--border);
  color: var(--text);
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}

.btn.primary {
  background: var(--accent);
  border-color: var(--accent);
}

.btn.ghost {
  background: transparent;
}

.card {
  min-width: 180px;
  max-width: 180px;
  background: var(--card);
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition:
    transform 150ms ease,
    box-shadow 150ms ease;
}

.card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.4);
}

.card__img {
  width: 100%;
  height: 100px;
  object-fit: cover;
  display: block;
}

.card__meta {
  padding: 8px;
}

.card__title {
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__progress {
  margin-top: 4px;
  width: 100%;
  height: 3px;
  border-radius: 999px;
  background: #1f2933;
  overflow: hidden;
}

.card__progress-bar {
  height: 100%;
  border-radius: inherit;
  background: var(--accent);
}

.player-page-main {
  padding: 16px 24px 40px;
}

.player {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.player__title {
  margin: 12px 0 8px;
  font-size: 22px;
}

.player__video {
  width: 100%;
  max-width: 1100px;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.8);
}

.player__desc {
  max-width: 1100px;
  color: var(--muted);
  margin-top: 10px;
}

.error {
  background: rgba(30, 58, 138, 0.3);
  color: #93c5fd;
  border: 1px solid #3b82f6;
  padding: 8px 12px;
  border-radius: 8px;
  margin-bottom: 12px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Estilos do Carrossel */
.carousel {
  position: relative;
  width: 100%;
  height: 500px;
  overflow: hidden;
  border-radius: 8px;
  margin-bottom: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.carousel-content {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 60px;
  position: relative;
  transition: all 0.5s ease-in-out;
}

.carousel-info {
  max-width: 600px;
  z-index: 2;
  animation: fadeIn 0.8s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.carousel-title {
  font-size: 3rem;
  font-weight: 800;
  margin: 0 0 16px 0;
  color: #fff;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.carousel-desc {
  font-size: 1.1rem;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.9);
  margin: 0 0 24px 0;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
}

.carousel-buttons {
  display: flex;
  gap: 16px;
  margin-bottom: 30px;
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  z-index: 3;
}

.carousel-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.4);
  border: none;
  padding: 0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.carousel-indicator:hover {
  background-color: rgba(255, 255, 255, 0.7);
  transform: scale(1.2);
}

.carousel-indicator.active {
  background-color: var(--accent);
  transform: scale(1.2);
}

/* Loading spinner */
.carousel-loading {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-soft);
  border-radius: 8px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  border-top-color: var(--accent);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsividade */
@media (max-width: 768px) {
  .carousel {
    height: 400px;
  }

  .carousel-content {
    padding: 30px 20px;
  }

  .carousel-title {
    font-size: 2rem;
  }

  .carousel-desc {
    font-size: 1rem;
  }

  .carousel-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .btn {
    width: 100%;
  }

  /* Navbar Mobile */
  .navbar__nav {
    display: none;
    /* Esconde links desktop */
  }

  .navbar__mobile-toggle {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    height: 24px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 60;
  }

  .navbar__mobile-toggle span {
    width: 30px;
    height: 3px;
    background: var(--text);
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;
  }

  /* Animação do Hamburger */
  .navbar__mobile-toggle.open span:first-child {
    transform: rotate(45deg);
  }

  .navbar__mobile-toggle.open span:nth-child(2) {
    opacity: 0;
    transform: translateX(20px);
  }

  .navbar__mobile-toggle.open span:nth-child(3) {
    transform: rotate(-45deg);
  }

  /* Menu Mobile Overlay */
  .navbar__mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--bg);
    z-index: 55;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 32px;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
  }

  .navbar__mobile-menu.open {
    transform: translateX(0);
  }

  .navbar__mobile-link {
    font-size: 1.5rem;
    color: var(--text);
    text-decoration: none;
    font-weight: 600;
    cursor: pointer;
    background: transparent;
    border: none;
  }

  .navbar__mobile-link:hover {
    color: var(--accent);
  }

  /* Sidebar */
  .sidebar {
    display: none;
    /* Esconde sidebar em mobile */
  }

  /* Layout adjustments */
  .content {
    padding: 16px 16px 80px;
    /* Mais padding bottom para mobile */
  }

  .row__container {
    padding: 0 16px;
    /* Ajuste de padding lateral */
  }

  /* Ajuste para grids de admin */
  .admin-grid {
    grid-template-columns: 1fr !important;
  }
}

/* Desktop Only Styles */
@media (min-width: 769px) {
  .navbar__mobile-toggle {
    display: none;
  }

  .navbar__mobile-menu {
    display: none;
  }
}
